  right: 0px;
  height: 100%;
  width: 100%;
}
.particles span.part-one {
    display: block;
    width: 12px;
    height: 12px;
    border-radius: 6px;
    background-color: #FF87B3;
    position: absolute;
    top: 0px;
    right: 10%;
}
.particles span.part-two {
    position: absolute;
    height: 14px;
    width: 14px;
    border-radius: 4px;
    left: 10%;
    top: 10%;
    background-color: #98C7FF;
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
}
.particles span.part-three {
    position: absolute;
    top: 70%;
    left: 0px;
    height: 10px;
    width: 40px;
    border-radius: 5px;
    background-color: #E5C7FF;
}
.particles span.part-four {
    position: absolute;
